.header {
  background: url("bg-pattern-intro-mobile.85d10c50.svg") no-repeat center center, linear-gradient(to right bottom, var(--gradient-light-red), var(--gradient-red) );
  height: max(80vh, 570px);
  border-bottom-left-radius: 100px;
}

.header__navbar--mobile {
  justify-content: space-between;
  align-items: center;
  padding: 48px 24px 0;
  display: flex;
}

.header__navbar--desktop {
  display: none;
}

.header__content {
  width: 100%;
  min-height: calc(100% - 88px);
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.header__title {
  font-family: var(--title-font);
  color: var(--white);
  text-align: center;
  font-size: 2rem;
  font-weight: 500;
}

.header__description {
  font-family: var(--body-font);
  color: var(--white);
  text-align: center;
  margin: 24px 0;
  font-size: 1.2rem;
  font-weight: 300;
}

.header__row {
  justify-content: center;
  gap: 16px;
  display: flex;
}

.header__link {
  font-family: var(--title-font);
  border: 1px solid var(--white);
  text-align: center;
  border-radius: 30px;
  padding: 16px;
  font-size: 1.25rem;
  font-weight: 700;
  transition: all .3s;
  display: inline-block;
}

.header__link:focus {
  box-shadow: var(--focus-shadow);
  outline: none;
}

.header__link:active {
  opacity: .5;
}

.header__link--start {
  background-color: var(--white);
  color: var(--light-red);
}

.header__link--learn {
  color: var(--white);
}

.header__link--learn:hover {
  color: var(--lighter-red);
  background-color: var(--white);
}

.background-black {
  z-index: -1000;
  opacity: .25;
  width: 100%;
  height: 100%;
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
}

.menu-lateral {
  width: 100vw;
  height: 100vh;
  background: none;
  padding: 48px 24px 24px;
  transition: all .3s;
  position: absolute;
  top: -150vh;
  left: 0;
  overflow: auto;
}

.menu-lateral--open {
  transition: all .3s;
  top: 0;
}

.menu-lateral__header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu-lateral__content {
  background-color: var(--white);
  text-align: center;
  width: min(500px, 100%);
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  margin-top: 34px;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 30px;
  padding-right: 30px;
}

.menu-lateral__lista {
  list-style: none;
}

.menu-lateral__button {
  color: var(--darker-blue);
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  column-gap: 10px;
  margin-bottom: 24px;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

hr {
  background: var(--greyish-blue);
  width: 100%;
  height: 1px;
  margin-top: 35px;
  margin-bottom: 35px;
}

.menu-lateral__link {
  color: var(--darker-blue);
  font-size: 1.2rem;
  font-weight: 700;
}

.menu-lateral__sign-up {
  color: var(--white);
  background: linear-gradient(90deg, var(--gradient-light-red), var(--gradient-red) );
  border-radius: 50px;
  margin-top: 24px;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  font-size: 1.2rem;
  font-weight: 700;
  display: inline-block;
}

.navbar__third-list {
  background-color: var(--greyish-blue);
  filter: brightness(1.15);
  width: 100%;
  border-radius: 5px;
  flex-direction: column;
  gap: 20px;
  padding: 24px;
  list-style: none;
  animation: toAppear .2s .2s backwards;
  display: none;
}

.third-list--open {
  display: flex;
}

.navbar__third-link {
  color: var(--darker-greyish-blue);
  filter: brightness();
  font-weight: 700;
}

.menu-lateral__arrow {
  transition: all .3s;
}

@keyframes toAppear {
  from {
    opacity: 0;
    transform: translateY(-24px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.introduction {
  margin-top: 95px;
}

.introduction__content {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.introduction__content > *, .introduction__articles > * {
  margin-bottom: 32px;
}

.introduction article:last-child {
  margin-bottom: 0;
}

.introduction__title {
  margin-bottom: 50px;
}

.introduction__illustration {
  width: min(450px, 100%);
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.introduction__illustration img {
  width: 100%;
}

.infra {
  width: 100%;
  background: url("bg-pattern-circles.d5778d88.svg") no-repeat bottom, linear-gradient(to right bottom, var(--gradient-dark-blue), var(--gradient-blue) );
  z-index: -1;
  border-radius: 0 100px;
  padding: 84px 24px;
  position: relative;
}

.infra__phones {
  width: min(768px, 100%);
  height: 400px;
  background: url("illustration-phones.021cbeaf.svg") center / contain no-repeat;
  margin-left: auto;
  margin-right: auto;
  transform: translateY(50%);
}

.infra__phones--desktop {
  display: none;
}

.infra__title {
  color: var(--white);
  margin-top: 150px;
  font-size: 2.5rem;
}

.infra__text {
  color: var(--white);
}

.advantages {
  margin-top: 110px;
  margin-bottom: 110px;
}

.advantages__content > *, .advantages__articles > * {
  margin-bottom: 32px;
}

.advantages__laptop {
  width: min(500px, 100%);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.advantages__laptop img {
  width: 100%;
}

.footer {
  background-color: var(--darker-black-blue);
  border-top-right-radius: 100px;
}

.footer__content {
  flex-direction: column;
  align-items: center;
  padding: 62px 80px 40px;
  display: flex;
}

.footer__title {
  font-family: var(--title-font);
  color: var(--white);
  margin-bottom: 40px;
  font-size: 2.5rem;
  font-weight: 700;
}

.footer__subtitle {
  color: var(--white);
  font-family: var(--title-font);
  margin-bottom: 24px;
  font-size: 1.25rem;
}

.footer__links {
  text-align: center;
  margin-bottom: 24px;
  list-style: none;
}

.footer__links > :not(:last-of-type) {
  margin-bottom: 8px;
}

.footer__link-item {
  color: var(--greyish-blue);
  font-family: var(--body-font);
  font-size: 1.25rem;
  font-weight: 300;
  text-decoration: none;
}

.footer__link-item:hover {
  text-decoration: underline;
}

.footer__link-item:focus {
  outline: 1px solid var(--white);
  opacity: .5;
}

:-webkit-any(.footer__title, .footer__subtitle, .footer__link-item)::-webkit-selection {
  background-color: var(--light-red);
}

:is(.footer__title, .footer__subtitle, .footer__link-item)::selection {
  background-color: var(--light-red);
}

@media screen and (min-width: 1124px) {
  .header {
    background: url("bg-pattern-intro-desktop.06dcf942.svg") no-repeat center center, linear-gradient(to right bottom, var(--gradient-light-red), var(--gradient-red) );
  }

  .header__navbar--mobile, .menu-lateral {
    display: none;
  }

  .header__navbar--desktop {
    max-width: 1440px;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    padding: 60px 54px 0;
    display: flex;
    position: relative;
  }

  .navbar-desktop__lista {
    align-items: center;
    column-gap: 34px;
    list-style: none;
    display: flex;
  }

  .navbar-desktop__logo {
    margin-right: 24px;
  }

  .navbar-desktop__button {
    color: var(--white);
    align-items: center;
    gap: 10px;
    font-size: 1rem;
    font-weight: 600;
    display: flex;
  }

  .navbar-desktop__button:hover {
    text-decoration: underline;
  }

  .navbar-desktop__arrow {
    transition: all .3s;
  }

  .navbar__third-list--desktop {
    background-color: var(--white);
    width: 12%;
    border-radius: 5px;
    flex-direction: column;
    gap: 20px;
    margin-top: 24px;
    padding: 24px;
    list-style: none;
    animation: toAppear .2s .2s backwards;
    display: none;
    position: absolute;
  }

  .third-list-desktop-open {
    display: flex;
  }

  .navbar-desktop__third-link {
    color: var(--darker-greyish-blue);
    font-weight: 700;
    transition: all .25s;
  }

  .navbar-desktop__third-link:hover {
    color: var(--black);
  }

  .navbar-desktop__link {
    color: var(--white);
    font-size: 1rem;
    font-weight: 700;
  }

  .navbar-desktop__sign-up {
    color: var(--light-red);
    font-family: var(--title-font);
    background: var(--white);
    border-radius: 50px;
    padding-top: .75rem;
    padding-bottom: .75rem;
    padding-left: 2rem;
    padding-right: 2rem;
    font-size: 1.25rem;
    font-weight: 700;
    transition: all .3s;
    display: inline-block;
  }

  .header__title {
    font-size: 3.4rem;
  }

  .introduction__title {
    text-align: center;
    margin-top: 80px;
    margin-bottom: 0;
    font-size: 2.4rem;
  }

  .introduction__content {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "articles img";
    padding-left: 50px;
    display: grid;
  }

  .introduction__content > * {
    margin-bottom: 0;
  }

  .introduction__illustration {
    width: 120%;
    grid-area: img;
  }

  .introduction__illustration img {
    width: 100%;
    transform: translateX(13%);
  }

  .introduction__articles > * {
    margin-bottom: 0;
  }

  .introduction__articles {
    grid-area: articles;
  }

  .introduction__articles article:first-child {
    margin-bottom: 62px;
  }

  .infra {
    height: 400px;
    background-position: 0 100%;
    align-items: center;
    column-gap: 40px;
    margin-top: 200px;
    margin-bottom: 200px;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 48px;
    padding-right: 48px;
    display: flex;
  }

  .infra__content {
    margin-bottom: 130px;
  }

  .infra__phones {
    display: none;
  }

  .infra__phones--desktop {
    display: block;
    transform: translateY(3%);
  }

  .advantages {
    padding-right: 50px;
  }

  .advantages__content {
    grid-template-columns: 1fr 1fr;
    margin-top: 220px;
    margin-bottom: 150px;
    display: grid;
  }

  .advantages__content > * {
    margin-bottom: 0;
  }

  .advantages__laptop {
    width: 130%;
  }

  .advantages__laptop img {
    width: 100%;
    transform: translateX(-29%);
  }

  .advantages__articles > * {
    margin-bottom: 0;
  }

  .advantages__articles article:first-child {
    margin-bottom: 62px;
  }

  .advantages__articles {
    align-self: center;
  }

  .footer__content {
    width: min(1200px, 100%);
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    margin-left: auto;
    margin-right: auto;
    padding-top: 80px;
    padding-bottom: 70px;
  }

  .footer__links {
    text-align: left;
  }
}

@media screen and (min-width: 1300px) {
  .introduction__title {
    transform: translateY(140%);
  }

  .introduction__content {
    grid-template-columns: .7fr 1fr;
    padding-left: 100px;
  }

  .infra {
    padding-left: 90px;
    padding-right: 90px;
  }

  .advantages {
    padding-right: 100px;
  }
}

@media screen and (min-width: 1400px) {
  .header__navbar--desktop {
    padding-left: 160px;
    padding-right: 160px;
  }

  .introduction__title {
    transform: translateY(180%);
  }

  .introduction__content {
    padding-left: 150px;
  }

  .infra {
    padding-left: 120px;
    padding-right: 120px;
  }

  .advantages {
    padding-right: 150px;
  }

  .advantages__content {
    grid-template-columns: 1fr .7fr;
  }
}

